<template>
  <div class="afterSaleDetail">
<!--    <van-nav-bar title="售后详情" :border="false" left-arrow @click-left="goBack"/>-->
      <navigation-bar  @onLeftClick="goBack" pageName="售后详情"></navigation-bar>
    <div class="afterSale" v-if="afterSaleDetail">
        <div class="box">
            <div class="status-box">
                <div class="status">
                    <div class="name">{{getStatusName()}}</div>
                    <div class="dis">{{getStatusRemind()}}</div>
                </div>
            </div>
            <div class="good-box">
                <div class="top">
                    <div class="left">
                        <div>{{afterSaleDetail.shopName}}</div>
                        <img :src="leftIcon" alt="">
                    </div>
                    <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 0">等待审核</div>
                    <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 1">等待退款</div>
                    <div class="right" v-if="flag == 2 && afterSaleDetail.billStatus == 0">退款成功</div>
                    <div class="right" v-if="flag == 2 && afterSaleDetail.billStatus == 1">申请未通过</div>
                    <div class="right" v-if="flag == 3">取消申请</div>
                </div>
                <div class="good" @click="goDetail">
                    <div class="good_img">
                        <img :src="afterSaleDetail.spuPicUrl" alt="">
                    </div>
                    <div class="good_info">
                        <div class="good_name">{{afterSaleDetail.spuName}}</div>
                        <div class="good_dis">
                            <div class="good_unit">
                                <div class="unit">{{afterSaleDetail.attrValueContent}}</div>
                                <div class="num">×{{afterSaleDetail.returnCount}}</div>
                            </div>
                            <div class="good_price">￥{{afterSaleDetail.returnRealTotal}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="order_info">
            <div class="item">
                <div class="left">售后编号</div>
                <div class="right">
                    <div class="billNo">{{afterSaleDetail.billNo}}</div>
                    <div class="copy" @click="copy(afterSaleDetail.billNo)">复制</div>
                </div>
            </div>
            <div class="item">
                <div class="left">申请时间</div>
                <div class="right">{{afterSaleDetail.createdTime}}</div>
            </div>
            <div class="item" v-if="flag == 2 && afterSaleDetail.billStatus == 0">
                <div class="left">退款方式</div>
                <div class="right">按付款时路径返还</div>
            </div>
            <div class="item" v-if="flag == 2 && afterSaleDetail.billStatus == 0">
                <div class="left">退款时间</div>
                <div class="right">{{afterSaleDetail.updatedTime}}</div>
            </div>
            <div class="item">
                <div class="left">退款原因</div>
                <div class="right">{{afterSaleDetail.returnReason}}</div>
            </div>
        </div>
        <div class="photo_box" v-if="afterSaleDetail.returnPicUrls && afterSaleDetail.returnPicUrls.length > 0">
            <div class="title">照片凭证</div>
            <div class="img_box">
                <img :src="item" alt="" v-for="(item, index) in afterSaleDetail.returnPicUrls" :key="index" @click="preview(afterSaleDetail.returnPicUrls)">
            </div>
        </div>
    </div>
    <div class="contact">
        <div class="left" @click="call">
            <img :src="phoneIcon" alt="">
            <span>联系商家</span>
        </div>
        <div class="right" v-if="flag == 1 && afterSaleDetail.billStatus == 0" @click="cancelApply">取消申请</div>
    </div>
    <Preview :images="images" ref="myPreview"></Preview>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import { cancelApply } from "../../services/orderListService";
import Preview from "@/components/preview/Preview.vue";
import phoneIcon from "@/assets/images/mall/call_small.png";
import leftIcon from "@/assets/images/common/home_icon_more.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "afterSaleDetail",
  data() {
    return {
      phoneIcon: phoneIcon,
      leftIcon: leftIcon,
      flag: 0,
      afterSaleDetail: {},
      images: []
    };
  },
  components: {
    Preview: Preview,
      NavigationBar
  },
  activated () {
    if(this.$route.params.detail) {
      this.afterSaleDetail = this.$route.params.detail;
      //  = {
      //     attrValueContent: "净含量:28ml×6片",
      //     billNo: "347273620591468544C3",
      //     billStatus: "0",
      //     contactPhone: "18561566407",
      //     createdTime: "2019-10-24 17:08:13",
      //     returnCount: 1,
      //     returnPicUrls: [],
      //     returnRealTotal: 0.16,
      //     returnReason: "退货",
      //     saleOrderBillNo: "347273221339865088C1",
      //     saleOrderSubBillNo: "347273308967264256C2",
      //     shopCode: "319019907922763776",
      //     shopName: "网易严选",
      //     skuCount: 1,
      //     skuNo: "343613708019957760",
      //     skuPrice: 89,
      //     spuCode: "343613707998986240",
      //     spuName: "二裂酵母 清透补水面膜  6片/盒 28ml×6片",
      //     spuPicUrl: "https://yanxuan-item.nosdn.127.net/2ce4616cbfd096c6878be0df37adc91b.png",
      //     updatedTime: "2019-10-24 17:18:54"
      // };
      this.flag = this.$route.params.flag;
    }
  },
  deactivated () {
    if (this.$store.state.preview.previewId) {
      this.$store.dispatch("setPreviewId", "");
    }
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 跳转到商品详情页面
     */
    goDetail() {
      // this.$router.push({
      //   name: "goodsDetail",
      //   params: {
      //     spuCode: this.afterSaleDetail.spuCode
      //   }
      // });
    },

    /**
     * 获取售后订单状态名称
     */
    getStatusName() {
      switch (this.flag * 1) {
        case 1:
          return this.afterSaleDetail.billStatus == 1 ? "待退款" : "待审核";
        case 2:
          return this.afterSaleDetail.billStatus == 1
            ? "申请未通过"
            : "退款成功";
        case 3:
          return "取消申请";
      }
    },

    /**
     * 获取售后订单状态提示语
     */
    getStatusRemind() {
      switch (this.flag * 1) {
        case 1:
          return "商品等待退款中···";
        case 2:
          return this.afterSaleDetail.billStatus == 1
            ? "审核人意见：审核不通过"
            : this.afterSaleDetail.updatedTime;
        case 3:
          return "申请已取消";
      }
    },

    /**
     * 取消申请售后
     */
    cancelApply() {
      cancelApply({
        billNo: this.afterSaleDetail.billNo
      }).then(
        result => {
          if (result.resultCode == 0) {
            this.$toast("申请已取消");
            this.flag = 3;
          } else {
            this.$toast(result.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 拨打电话
     */
    call() {
      const platform = navigator.userAgent;
      const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
      console.log(isiOS);
      if (isiOS) {
        window.location.href = "tel:" + this.afterSaleDetail.contactPhone;
      } else {
        Dialog.confirm({
          title: "拨打电话",
          message: this.afterSaleDetail.contactPhone
        })
          .then(() => {
            window.location.href = "tel:" + this.afterSaleDetail.contactPhone;
          })
          .catch(() => {
            // on cancel
            Dialog.close;
          });
      }
    },

    /**
     * 复制订单号
     */
    copy(billNo) {
      cordova.plugins.clipboard.copy(
        billNo,
        function() {
          Toast("复制成功");
        },
        function() {
          console.log("error");
        }
      );
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      this.images = src;
      this.$refs.myPreview.setIndex(index);
      this.$refs.myPreview.setIsShowPreview(true);
      this.$store.dispatch("setPreviewId", this.$refs.myPreview);
    },
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.afterSaleDetail {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  .afterSale {
    flex: 1;
    overflow: auto;
    background: #ffffff;
    .box {
      position: relative;
      width: 100%;
      height: 245px;
      .status-box {
        height: 140px;
        width: 100%;
        background-image: linear-gradient(-199deg, #ff8894 0%, #fb5364 97%);
        padding: 30px 0 0 20px;
        box-sizing: border-box;
        .name {
          height: 25px;
          line-height: 25px;
          font-family: PingFangSC-Medium;
          font-size: 18px;
          color: #ffffff;
        }
        .dis {
          height: 20px;
          line-height: 20px;
          margin-top: 2px;
          font-size: 14px;
          color: #ffffff;
        }
      }
      .good-box {
        width: 345px;
        height: 146px;
        position: absolute;
        bottom: 0;
        left: 15px;
        background: #ffffff;
        box-shadow: 0 0 10px 0 rgba(214, 214, 214, 0.5);
        border-radius: 3px;
        .top {
          display: flex;
          padding: 0 15px;
          justify-content: space-between;
          align-items: center;
          height: 40px;
          line-height: 40px;
          .left {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            height: 40px;
            line-height: 40px;
            font-family: PingFangSC-Medium;
            font-size: 14px;
            color: #333333;
            div {
              max-width: 240px;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            img {
              margin-left: 4px;
              width: 8px;
              height: 16px;
            }
          }
          .right {
            font-family: PingFangSC-Regular;
            font-size: 12px;
            color: #333333;
          }
        }
        .good {
          width: 100%;
          height: 104px;
          padding: 15px;
          box-sizing: border-box;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          .good_img {
            width: 74px;
            height: 74px;
            img {
              width: 74px;
              height: 74px;
            }
          }
          .good_info {
            width: 230px;
            margin-left: 10px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            .good_name {
              font-family: PingFangSC-Medium;
              font-weight: bold;
              font-size: 14px;
              color: #333333;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 2;
              -webkit-box-orient: vertical;
              width: 160px;
              line-height: 22px;
            }
            .good_dis {
              width: 100%;
              display: flex;
              justify-content: space-between;
              align-items: center;
              margin-top: 4px;
              .good_unit {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                font-family: PingFangSC-Regular;
                font-size: 12px;
                color: #333333;
                .unit {
                  width: 135px;
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                }
                .num {
                  color: #9a9a9a;
                  margin-left: 2px;
                }
              }
              .good_price {
                font-family: PingFangSC-Medium;
                font-size: 12px;
                color: #f43d3d;
                max-width: 80px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
    .order_info {
      width: 345px;
      margin: 14px auto;
      background: #ffffff;
      box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
      border-radius: 4px;
      padding: 20px;
      box-sizing: border-box;
      .item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        min-height: 20px;
        line-height: 20px;
        margin-bottom: 10px;
        .left {
          font-family: PingFangSC-Regular;
          font-size: 14px;
          color: #9a9a9a;
        }
        .right {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          font-size: 14px;
          color: #333333;
          max-width: 240px;
          text-align: left;
            .billNo{
                height: 20px;
                line-height: 20px;
            }
          .copy {
            font-weight: normal;
            width: 52px;
            height: 20px;
            border: 1px solid #666666;
            border-radius: 14.5px;
            box-sizing: border-box;
            margin-left: 6px;
            font-size: 12px;
            color: #666666;
            text-align: center;
            line-height: 20px;
          }
        }
      }
      .item:last-child {
        margin: 0;
      }
    }
    .photo_box {
      width: 345px;
      margin: 14px auto;
      background: #ffffff;
      box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
      border-radius: 4px;
      padding: 20px;
      box-sizing: border-box;
      .title {
        width: 100%;
        height: 20px;
        line-height: 20px;
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #9a9a9a;
      }
      .img_box {
        width: 100%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: nowrap;
        overflow-x: scroll;
        margin-top: 10px;
        height: 80px;
        img {
          width: 80px;
          height: 80px;
          border-radius: 4px;
          margin-right: 15px;
        }
        img:last-child {
          margin: 0;
        }
      }
    }
  }
  .contact {
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);
    .left {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      img {
        width: 18px;
        height: 18px;
        margin-right: 4px;
      }
      span {
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #3a3a3a;
        line-height: 50px;
      }
    }
    .right {
      width: 92px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-family: PingFangSC-Medium;
      font-size: 14px;
      color: #fb5768;
      background: #ffffff;
      border: 1px solid #fb5768;
      border-radius: 14.5px;
    }
  }
}
</style>
